/* src/components/Header.css */
.header {
  padding-left: 150px;
  padding-top: 80px;
  padding-bottom: 80px;
  background-image: url('../../public/LosPadres1.jpg');
  /* Update with the correct path */
  background-size: cover;
  background-position: center;
  color: #f6f6f6;
  text-align: left;
}

.header h1 {
  font-family: 'Varela', sans-serif;
  font-size: 38px;
  color: #f6f6f6;
  /* Light grey color */
}

.header p {
  font-family: 'Verdana', sans-serif;
  font-size: 18px;
  color: #FFFFFF;
  /* White color */
}

/* Add to your existing Header.css file */
.profile-container {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
}

.profile-name {
  margin-right: 10px;
  color: rgb(255, 217, 0);
  /* Set font color to yellow */
  font-family: 'Times New Roman', serif;
  font-size: 20px;
  /* Change font to a serif font */
  /*-webkit-text-stroke: 0.5px black;
  /* Adding a black border for webkit browsers */
  text-shadow:
    -0.5px -0.5px 0 #000,
    0.5px -0.5px 0 #000,
    -0.5px 0.5px 0 #000,
    0.5px 0.5px 0 #000;
  /* Simulating a black border for other browsers */
}

.profile-photo {
  width: 50px;
  /* Adjust width as needed */
  height: 50px;
  /* Adjust height as needed */
  object-fit: cover;
  /* This ensures the image covers the area without being distorted */
  border: #111111;
  border-style: solid;
  border-width: 1px;
  /* Optional: if you want the photo to be rounded */
}