/* src/components/AddressForm.css */
.address-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 20px;
  background: #f8fff5;
  /* Updated background color */
  /* Center the container */
  /* src/components/AddressForm.css */
  margin-left: auto;
  /* Adjusts margin to auto to center the content */
  margin-right: auto;
  /* Adjusts margin to auto to center the content */
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  /* Space between the title and the form */
}

.address-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Center form elements vertically */
  width: 100%;
  max-width: 420px;
  margin-bottom: 30px;
  /* Use full width of the container */
}


.address-input,
.submit-btn {
  width: 90%;
  /* Make input and button take up most of the container width */
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.submit-btn {
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  width: auto;
  border-radius: 6px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  padding: 10px 20px;
  margin-top: 10px;
  /* Allow the button to size according to its content */
}

.map-display {
  width: 400px;
  height: 250px;
  background-color: #e0e0e0;
  border-radius: 8px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* Style for the loading spinner */
.loading-spinner {
  border: 3px solid rgba(0, 0, 0, 0.1);
  /* Light grey border */
  border-top: 3px solid #34db4a;
  /* Blue border at the top */
  border-radius: 50%;
  /* Circular shape */
  width: 30px;
  /* Size of the spinner */
  height: 30px;
  /* Size of the spinner */
  animation: spin 2s linear infinite;
  /* Apply the spinning animation */
}