/* src/components/StatsSection.css */
.stats-section {
  display: flex;
  align-items: center;
  justify-content: space-around;
  overflow-x: auto;
  /* Allows scrolling horizontally if the container is too small */
  white-space: nowrap;
  /* Prevents wrapping of child elements */
  padding: 10px 0;
  background-color: #f0f0f0;
  /* Light background for contrast with #333 text */
}

.service {
  display: inline-flex;
  /* Display services inline with flex properties */
  flex-direction: column;
  align-items: center;
  /* Align items in the center for a sleek look */
  justify-content: center;
  margin: 0 10px;
  /* Space between each service */
  padding: 10px;
  min-width: 120px;
  /* Minimum width for each service */
  background-color: #f0f0f0;
  /* Light background to ensure text color #333 is visible */
  color: #333;
  /* Dark text color for visibility */
  font-family: Arial, sans-serif;
  /* Arial font for all text within .service */
  max-width: 275px;
  /* Adjust as needed */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.service h2 {
  font-size: 40px;
  /* Larger font size for the "big number" */
  margin: 5px 0;
  /* Tightens spacing around headings */
}

.service p {
  margin: 5px 0;
  /* Consistent margin for simplicity and alignment */
}

.service button {
  padding: 10px 20px;
  margin-top: 10px;
  /* Spacing above the button */
  background-color: #3898ec;
  /* A bright, inviting color for contrast */
  color: white;
  border: none;
  border-radius: 6px;
  /* Soften the button corners */
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
}

.service button:hover {
  background-color: #0056b3;
  /* Darker shade on hover for interaction feedback */
}